.nav{
    position: fixed;
    top:0;
    padding: 20px;
    height: 50px;
    width: 100%;
    z-index: 1;
    box-sizing: border-box;
    transition: all 0.5s ease-in 0s;
}
.nav__content{
    display: flex;
    justify-content: space-between;
}
.nav__content .logo{
    width: 80px;
    position:fixed;
    cursor: pointer;
    top: 5px;
    left: 20px;
    /* bottom: 10PX; */
}
.nav_avatar{
    width:30px;
    position:fixed;
    cursor: pointer;
    top: 10px;
    right:20px ;
}
.nav__black{
    background-color: #000;
}